	// 引入商品列表组件
import goodsList from '../../components/goods-list/goods-list.vue';
export default {
	components: { goodsList },
	data() {
		return {
			// 轮播图
			swiperList: [],
			// 商品列表
			goodsList: [],
			// 商品列表的初始页码
			page: 1,
			// 触底线的显示和隐藏
			sole: false,
			// 点击4个导航跳转的地址
			navurl: ['/pages/goods/goods', '/pages/contact/contact', '/pages/pics/pics', '/pages/videos/videos']
		};
	},
	onLoad() {
		// 获取轮播图数据
		this.getSwiperList();
		// 获取商品列表数据
		this.getGoodsList();
	},
	// 页面触底的操作
	onReachBottom() {
		if(this.goodsList.length < this.page*10 ){
			return this.sole = true 
		}
		this.page++;
		console.log(123);
		// 获取商品列表数据
		this.getGoodsList();
	},
	methods: {
		// 获取轮播图数据
		getSwiperList() {
			uni.request({
				url: 'https://www.uinav.com/api/public/v1/home/swiperdata',
				success: res => {
					if (res.statusCode !== 200) {
						uni.showToast({
							title: '获取轮播图数据失败',
							icon: 'none',
							duration: 2000
						});
					}
					this.swiperList = res.data.message;
					console.log(this.swiperList);
				}
			});
		},
		// 获取商品列表数据
		async getGoodsList() {
			const res = await uni.get('/api/getgoods?pageindex=' + this.page);
			if (res[1].data.status !== 0) {
				uni.baseToast('获取商品失败');
			}
			console.log(res);
			this.goodsList = [...this.goodsList, ...res[1].data.message];
			console.log(this.goodsList);
		},
		// 点击nav导航区域
		navClick(url) {
			uni.navigateTo({
				url
			});
		}
	}
};
